.key {
  width: 100px;
  height: 100px;
  border: 1px solid white;
  border-radius: 0;
  font-size: 20px;
  background-color: #bdc8d0;
  cursor: pointer;
  box-sizing: border-box;
}

.key.wide {
  width: 200px;
}

.key.blue {
  background-color: #299bff;
  color: white;
}

.key:hover {
  background-color: #92a4b1;
}

.key.blue:hover {
  background-color: #1b8bec;
}

.screen {
  height: 100px;
  width: 400px;
  background-color: #3b3e41;
  color: white;
  padding: 8px;
  font-size: 24px;
  box-sizing: border-box;
}

.mem {
  width: 80px;
  height: 80px;
  background-color:grey;
}

.mem:hover {
  background-color:aquamarine;
}

@media screen and (max-width: 400px) {
  .key {
    width: 50px;
    height: 50px;
    font-size: 12px;
  }
  .key.wide {
    width: 100px;
    height: 50px;
  }

  .mem {
    width: 40px;
    height: 40px;
    font-size: 12px;
  }

  .screen {
    height: 50px;
    width: 200px;
  }

  @media screen and (max-width:200px) {
    .key {
      width: 25px;
      height: 25px;
    }
    .key.wide {
      width: 50px;
      height: 25px;
    }

    .mem {
      width: 20px;
      height: 20px;
      font-size: 6px;
      padding: 4px;
    }
  
    .screen {
      height: 25px;
      width: 100px;
      font-size: 6px;
    }
   
  }
}
